import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {Img} from "core/ui/controls/img"
import {Images} from "games/mnemonics/wordassociation/assets"

const durations = {
  move: 1000
}

export class IntroComponent extends GameComponent implements OnInit {
  positions = Sizes.create({
    size: {
      w: 100,
      h: 50
    },
    decor: [
      [400, 300],
      [400, -300],
      [-400, 300],
      [-400, -300],
    ]
  })

  bg = this.addImage(Images.bg)

  decors: Img[]
  _value: 0
  get value() {
    return this._value
  }

  set value(v) {
    this._value = v
  }

  playTween() {
    const tweens = this.decors.map(x => {
      return Tweens.start({
        target: x,
        to: {pivotX: 0, pivotY: 0, angle: 360 * 2},
        duration: durations.move,
      })
    })
    return tweens[0]
  }

  onInit(): void {
    this.decors = this.positions.decor.map((p, i) => {
      const img = this.addImage(Images.decor(i))
        .updatePos(
          Sizes.w2 - this.positions.size.w,
          Sizes.h2 - this.positions.size.h)
      img.pivot.set(p[0], p[1])
      return img
    })
  }

}




