import {createLevelsFromArray} from "core/types/logic"
import {MnemonicsWords} from "games/mnemonics/common/mnemonicsWords"

export interface Level {
  groupCount: number
  groupSize: number
  answerCount: number
  correctAnswerCount: number
  wordDifficulty: MnemonicsWords.WordDifficulty
}

function level(groupCount: number, groupSize: number, correctAnswerCount: number, answerCount: number,
  wordDifficulty: MnemonicsWords.WordDifficulty
): Level {
  return {groupCount, groupSize, answerCount, correctAnswerCount, wordDifficulty}
}

export const levels = createLevelsFromArray([
  level(3, 2, 1, 3, "easy"),
  level(4, 2, 1, 3, "easy"),

  level(3, 3, 1, 3, "easy"),

  level(6, 2, 1, 5, "easy"),

  level(4, 3, 1, 5, "easy"),

  level(8, 2, 1, 8, "easy"),

  level(3, 3, 2, 3, "normal"),
  level(6, 3, 1, 5, "normal"),
  level(4, 3, 2, 5, "normal"),
  level(8, 3, 1, 8, "normal"),

  level(6, 3, 2, 5, "hard"),
  level(8, 3, 2, 8, "hard"),
])



