import {CoreColors, CoreFonts} from "core/assets/fonts"
import {Signals} from "core/ui/animations/signals"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn} from "core/ui/controls/btn"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {BtnDefaultImg} from "core/ui/controls/GraphicsImg"
import {AnswerComponent} from "games/mnemonics/wordassociation/components/answerComponent"
import {IntroComponent} from "games/mnemonics/wordassociation/components/introComponent"
import {Fonts, Images, Translations} from "../assets"
import {Task} from '../logic'
import {QuestionComponent} from './questionComponent'

const durations = {}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    marginTop: 70,
    pointsTop: [600, 350],
    pointsBottom: [600, 750],
    decor: [
      // [400,1030],
      // [-100,-100],
      // // [280,120],
      // [1400,1050],
      // [100,500],
      // [1800,600],
    ],
    btnSize: {width: 500, height: 100},
  })
  biPositions = Sizes.bi({
    rememberBtn: {x: Sizes.widthNormal / 2, y: 1120},
    question: {x: Sizes.widthNormal / 2, y: 661},
    speed: {x: Sizes.widthNormal / 2, y: 210},
  }, {
    rememberBtn: {x: Sizes.widthNormal / 2, y: 1210},
    question: {x: Sizes.widthNormal / 2, y: 700},
    speed: {x: Sizes.widthNormal / 2, y: 110},
  })

  bg = this.addImage(Images.bg)
  points = this.addImage(Images.point).setPos(this.positions.pointsBottom)

  showQuestion(visible: boolean) {
    const pointsPos = visible ? this.positions.pointsBottom : this.positions.pointsTop
    this.points.setPos(pointsPos)
    const task = this.getCurrentTask<Task>()
    const key = visible ? Translations.tutor : Translations.question(task.correctAnswerCount)
    this.tutorial.setProps(this.translate(key))
    this.question.visible = visible
    this.answer.visible = !visible
    this.rememberBtn.visible = visible
  }

  tutorial: TutorialTextComponent

  question = this.add(this.createComponent(QuestionComponent)).anchorCenter()
    .setBiPos(this.biPositions.question)

  answer = this.add(this.createComponent(AnswerComponent)).anchorCenter()
    .setBiPos(this.biPositions.question).fluentUpdate(x => x.visible = false)

  rememberBtn = this.add(
    new Btn(
      this.translate(Translations.remember),
      () => this.showQuestion(false), CoreFonts.button(CoreColors.black), new BtnDefaultImg({
        color: Fonts.yellow,
        size: this.positions.btnSize
      })))
    .anchorCenter()
    .setBiPos(this.biPositions.rememberBtn)

  introAnimation(): { start: () => void; onComplete: Phaser.Signal } {
    const intro = this.add(this.createComponent(IntroComponent))
    const tween = intro.playTween()
    return {
      start: () => {
        tween.start()
      },
      onComplete: Signals.bind(tween.onComplete, () => {
        return ControlUtl.hideAndDestroy(intro)
      })
    }
  }

  onInit(): void {
    this.positions.decor.forEach((p, i) => {
      this.addImage(Images.decor(i)).setPos(p)
    })
    this.statusOptions.waitCompleteBeforeNewTask = true
    this.introOptions.runOnNewTaskAfter = true
    this.startIntro()

    this.tutorial = this.add(this.createComponent(TutorialTextComponent)
      .make({marginTop: this.positions.marginTop}))
  }

  onNewTask(task: Task) {
    this.tutorial.setProps(this.translate(Translations.tutor))
    if (!this.isFirstTask) {
      this.showQuestion(true)
    }
  }

}




