import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {Control} from "core/ui/controls/control"
import {Txt} from "core/ui/controls/txt"
import {Fonts, Images} from "games/mnemonics/wordassociation/assets"
import * as _ from 'lodash'

export class WordListComponent extends GameComponent<{ hideWords: boolean }> {

  positions = Sizes.create({
    wordsGroup: {x: 30},
    margin: {x: 0}
  })

  biPositions = Sizes.bi({}, {})

  bg = this.addImage(Images.podloshka)
  wordsGroup = this.add(new Control()).anchorCenterY().setPos([this.positions.wordsGroup.x, this.height / 2])
  texts: Txt[] = []
  index = 1

  setWords(words: string[], additionalWordsCount = 0) {
    this.index = words.length
    this.wordsGroup.removeAll(true)
    this.texts = words.concat(_.times(additionalWordsCount, () => ""))
      .map(word => this.wordsGroup.add(new Txt(word, Fonts.word)))
    Sizes.tableColumn(this.texts, this.positions.margin)
    this.wordsGroup.updatePivot()
    return this
  }

  addWord(word: string) {
    this.texts[this.index].text = word
    this.index += 1
  }

  onPropsUpdate() {
  }
}



