import {Numbers} from "core/utils/numbers"
import {RandomGenerator} from "core/utils/randomGenerator"
import {Undef} from "core/utils/undef"
import * as _ from "lodash"

export class CircleSeq {
  index = 0

  constructor(private array: number[]) {}

  next() {
    const index = this.index
    this.index = (this.index + 1) % this.array.length
    return this.array[index]
  }

  nextArray(count: number) {
    return _.range(count).map(() => this.next())
  }
}

export class UniqSeq {
  private index = 0
  private list: number[] = []

  constructor(public max: number, private rng = new RandomGenerator()) {
    this.reset()
  }

  checkMax(newMax: number) {
    if (newMax != this.max) {
      this.max = newMax
      this.reset()
    }
    return this
  }

  reset() {
    let newList = this.rng.uniqArray(this.max, this.max)
    if (this.list.length > 0) {
      const last = _.drop(this.list, Math.floor(this.list.length * 0.6))
      const first = _.difference(newList, last)
      newList = first.concat(this.rng.shuffle(last))
    }
    this.list = newList
  }

  private updateIndex() {
    this.index += 1
    if (this.index >= this.list.length) {
      this.reset()
      this.index = 0
    }
  }

  nextArray(count: number) {
    return _.range(count).map(() => this.next())
  }

  next(max?: number) {
    if(max){
      this.checkMax(max)
    }
    const v = this.list[this.index]
    this.updateIndex()
    return v
  }

  nextSkip(v: number): number {
    const n = this.next()
    if (this.list.length > 1 && n == v) {
      return this.nextSkip(v)
    }
    return n
  }

  nextFrom<T>(arr: T[], skip?: T) {
    this.checkMax(arr.length)
    return arr[Undef.is(skip) ? this.next() : this.nextSkip(arr.indexOf(skip))]
  }

  nextSubsetFrom<T>(arr: T[], count: number) {
    return _.range(count).map(() => this.nextFrom(arr))
  }
}



