import {CoreFonts} from "core/assets/index"
import {CoreImages} from "core/assets/images"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Sizes} from "core/ui/common/sizes"
import {TextAlign} from "core/ui/common/types"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {PanelComponent} from "core/ui/components/games/gameUI/panelComponent"
import {Txt} from "core/ui/controls/index"
import {Btn, IconBtn} from "core/ui/controls/btn"
import {screenSizeService} from "core/ui/services/instances"
import {Undef} from "core/utils/undef"

export type TutorialTextProps = {
  marginTop?: number
  minHeight?: number,
  stepCount?: number,
  textAlign?: TextAlign,
  panel?: { size?: { width?: number, height?: number }, color?: number, alpha?: number }
}

export class TutorialTextComponent extends GameComponent<string> { //TODO rename tutrialComponent
  positions = Sizes.create({
    marginTop: 40,
    textMarginX: 40,
    padding: 80,
    buttonMargin: 10,
    btnWidth: 200,
    btn: {x: 250, y: 90}
  })

  positionsPortrait = Sizes.update(this.positions, {
    marginTop: 20,
    textMarginX: 40,
    padding: 40,
  })

  getPositions = () => screenSizeService.isPortrait ? this.positionsPortrait : this.positions

  private text: Txt
  private back: PanelComponent
  private nextBtn: Btn
  marginTop = 0
  minHeight?: number
  stepCount?: number
  panel: { size?: { width?: number, height?: number }, color?: number, alpha?: number }

  make(props: TutorialTextProps = {}) {
    this.marginTop = props.marginTop || this.getPositions().marginTop
    this.minHeight = props.minHeight
    this.panel = props.panel || {}
    this.back = this.add(new PanelComponent()).anchorCenterX()
    this.text = this.add(new Txt("", {...CoreFonts.tutorial(false), align: props.textAlign || "left"})).anchorCenter()
    const stepCount = props.stepCount || 1
    if (stepCount > 1) {
      this.tutorialStore.setStepCount(stepCount)
      this.nextBtn = this.back.add(new IconBtn("далее", CoreImages.next, () => {
        this.tutorialStore.nextTutorialState()
      }, this.positions.buttonMargin))
      CommonAnimations.highlight(this.nextBtn)
      this.onChangeForRun(() => this.tutorialStore.isLastStep, isLastStep => {
        this.nextBtn.visible = !isLastStep
      })
    }
    this.onChangeForRun(() => screenSizeService.screenSize, ({width, height}) => {
      // const btnWidth = this.nextBtn ? this.positions.btnWidth : 0
      const btnWidth = 0
      this.text.textControl.wordWrapWidth = screenSizeService.realWidth - this.getPositions().textMarginX * 2 - btnWidth
      this.updateSize()
    })
    return this

  }

  updateSize() {
    const textHeight = this.text.height + this.getPositions().padding
    const height = this.minHeight ? Math.max(this.minHeight, textHeight) : textHeight

    this.back.updatePos(Sizes.w2, this.marginTop - screenSizeService.topOffset)
    const width = Undef.map(this.panel.size, x => x.width) || screenSizeService.realWidth - this.getPositions().padding
    this.back.setProps({...this.panel, size: {width, height}})

    this.text.updatePos(Sizes.w2, this.back.height / 2 + this.back.y)
    this.text.updatePivot()
    this.back.visible = !!this.text.text
    if (this.nextBtn) {
      this.nextBtn.updatePos(this.back.width - this.positions.btn.x, this.back.height - this.positions.btn.y)
    }
    this.back.updatePivot()
  }

  onPropsUpdate(): void {
    this.text.text = this.props
    this.updateSize()
  }

}



