import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Control} from "core/ui/controls/control"
import {WordListComponent} from "games/mnemonics/wordassociation/components/wordListComponent"
import {Task} from "../logic"

const count = 9

const biValues = BiValue.create({
  scale: 1
}, {
  scale: 0.75
})

const columnCounts: Record<number, number> = {
  4: 2,
}

export class QuestionComponent extends GameComponent implements OnInit {

  positions = Sizes.create({})

  biPositions = Sizes.bi({
    margin: {x: 28},
  }, {
    margin: {x: 20},
  })

  wordsGroup = this.add(new Control())

  onNewTask(task: Task): void {
    this.createControls()
  }

  createControls() {
    this.wordsGroup.removeAll(true)
    const task = this.getCurrentTask<Task>()
    const texts = task.groups.map(x =>
      this.createComponentWithProps(WordListComponent, {hideWords: false})
        .setWords(x)
    )
    this.wordsGroup.addChildren(texts)
    const colCount = this.isPortrait ? 2 : (columnCounts[texts.length] || 3)
    Sizes.tableFromList(texts, colCount, this.current(this.biPositions.margin))
    this.updatePivot()
  }

  onInit(): void {
    this.onBiValueChange(biValues.scale, (c, v) => {
      this.symmetricScale = v
      this.createControls()
    })
  }

}



