import {GameLogic} from "core/logic/index"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/index"
import {MnemonicsWords} from "games/mnemonics/common/mnemonicsWords"
import * as _ from 'lodash'
import {Level} from "./levels"

type WordGroup = string[]
type Question = {
  words: string[],
  correctAnswers: string[],
  answerOptions: {
    word: string
    answerId: string
    isCorrect: boolean
  }[]
}

export type Task = {
  groups: WordGroup[]
  questions: Question[]
  correctAnswerCount: number
  answers: { groups: string[][] }
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()

  words = MnemonicsWords.getWords()
  wordsSeq = new UniqSeq(0)

  nextTask(level: Level) {
    const groups = _.range(level.groupCount).map(() =>
      this.wordsSeq.nextSubsetFrom(this.words[level.wordDifficulty], level.groupSize))
    const allWords = _.flatten(groups)
    let prevCorrectAnswers: string[] = []
    const questions: Question[] = this.rng.shuffle(groups).map((group, i) => {
      const [correctAnswers, words] = this.rng.split(group, level.correctAnswerCount)
      const answerWords = _.difference(allWords, group.concat(prevCorrectAnswers))
      const wrondAnswerWords = this.rng.take(answerWords, level.answerCount - correctAnswers.length)
      const answerOptions = this.rng.shuffle(
        correctAnswers.map(word => ({word, answerId: word, isCorrect: true})),
        wrondAnswerWords.map(word => ({word, answerId: word + i, isCorrect: false})))
      prevCorrectAnswers = correctAnswers
      return {
        words,
        correctAnswers,
        answerOptions
      }
    })

    const answers = {groups: questions.map(x => x.correctAnswers)}
    const correctAnswerCount = level.correctAnswerCount

    const task: Task = {
      groups,
      correctAnswerCount,
      questions,
      answers
    }
    return task
  }

}





