import {Sizes} from "core/ui/common/sizes"
import {Size} from "core/ui/common/types"
import {Component} from "core/ui/components/component"
import {TextOnCenterBehavior} from "core/ui/controls/behaviors/textOnCenterBehavior"
import {GraphicsImg} from "core/ui/controls/GraphicsImg"

export type PanelProps = { size: Size, color?: number, alpha?: number }

export class PanelComponent extends Component<PanelProps> {
  positions = Sizes.create({
    radius: 20,
  })

  private textBehavior: TextOnCenterBehavior
  private control: GraphicsImg

  // private control: NinePatchComponent

  constructor() {
    super()

    // this.control = this.add(new NinePatchComponent(CoreImages.panelList))
    this.control = this.add(new GraphicsImg(g => {
      if (this.props) {
        g.beginFill(this.props.color, this.props.alpha || 0.4)
        g.drawRoundedRect(0, 0, this.props.size.width, this.props.size.height, this.positions.radius)
        g.endFill()
      }
    }))
  }

  update() {
  }

  onPropsUpdate(): void {

    this.control.updateGraphics()
    // this.control.setSize(this.props.size)
  }

}



