import {CoreColorNumbers, CoreColors} from "core/assets/fonts"
import {Sizes} from "core/ui/common/sizes"
import {Control} from "core/ui/controls/control"
import {phaserService} from "core/ui/services/instances"
import * as Phaser from 'phaser-ce'

export class GraphicsImg extends Control {
  img: Phaser.Sprite

  constructor(private draw: (g: Phaser.Graphics) => void) {
    super()
    this.img = this.add(phaserService.drawSprite(draw))
  }

  updateGraphics() {
    if (this.game) {
      const graphics = this.game.make.graphics(0, 0)
      this.draw(graphics)
      this.img.loadTexture(graphics.generateTexture())
      graphics.destroy(true)
    }
  }
}


export class BtnDefaultImg extends GraphicsImg {

  constructor(props: {
    color: number,
    size?: {width: number, height: number}
  }) {
    const size = props.size || Sizes.create({width: 500, height: 100})
    super(g => {
      g.beginFill(props.color)
      g.drawRoundedRect(0, 0, size.width, size.height, size.height/2)
    })
  }

}



