import {AnswerGroups} from "core/logic/answers"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {AnswerBtnBehavior} from "core/ui/controls/behaviors/answerBtnBehavior"
import {ImageBtn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {Fonts, Images} from "games/mnemonics/wordassociation/assets"
import {WordListComponent} from "games/mnemonics/wordassociation/components/wordListComponent"
import {Task} from "../logic"

const biValues = BiValue.create({
  scale: 1
}, {
  scale: 0.8
})
const durations = {
  delay: 500
}

export class AnswerComponent extends GameComponent implements OnInit {

  positions = Sizes.create({
    answerMarin: 0
  })
  biPositions = Sizes.bi({
    buttons: {y: 300},
    margin: {x: 40, y: 20},
  }, {
    buttons: {y: 500},
    margin: {x: 60, y: 60},
  })

  words = this.add(this.createComponentWithProps(WordListComponent, {hideWords: true})).anchorCenterX()
  buttonGroups = this.add(new Control()).setBiPos(this.biPositions.buttons)
  private buttons: ImageBtn[] = []

  onNewTask(task: Task): void {
    this.createQuestion()
  }

  get answerChecker() {
    return this.context.gameService.gameCycleService.state.answers as AnswerGroups
  }

  onAnswer(answer: ILastAnswer): void {

    if (AnswerStatus.isCorrect(answer.status)) {
      this.words.addWord(answer.answerId)
    }

    if (answer.status == AnswerStatus.CorrectPartial && this.answerChecker.currentSeq.isEmpty) {
      this.disableInputOn(this.delay(durations.delay)).addOnce(() => {
        this.createQuestion()
      })
    } else if (answer.status == AnswerStatus.Error) {
      AnswerBtnBehavior.findByAnswerId(this.buttons, answer.answerId).textControl.fill = Fonts.red
      const answers = (answer.answerChecker as AnswerGroups).currentSeq.restAnswers
      answers.forEach(answerId => {
          AnswerBtnBehavior.findByAnswerId(this.buttons, answerId).textControl.fill = Fonts.green
          this.words.addWord(answerId)
        }
      )

    }
    // const correctAnswer = (answer.levelTask.task as Task).answers[0]
    // AnswerBtnBehavior.findByAnswerId(this.buttons, correctAnswer).textControl.fill = CoreColors.darkGreen
    // if (AnswerStatus.Error == answer.status) {
    //   AnswerBtnBehavior.findByAnswerId(this.buttons, answer.answerId).textControl.fill = CoreColors.wrong
    // }
  }

  createQuestion() {
    const task = this.getCurrentTask<Task>()
    const question = task.questions[this.answerChecker.index]
    this.buttonGroups.removeAll(true)
    this.buttons = question.answerOptions.map(word => {
      const btn = new ImageBtn(word.word, this.gameKey(Images.button_stroke), () => {
      }, Fonts.button)
      return this.buttonGroups.add(this.addAnswerBehavior(btn, word.answerId, {
        dark: true,
        margin: this.positions.answerMarin
      }))
    })
    this.updatePositions()
    this.words.visible = false
    this.words.setPos({x: this.buttonGroups.width / 2}).setWords(question.words, task.correctAnswerCount)
    this.words.visible = true
    this.updatePivot()
    this.answersAreReady()
  }

  updatePositions() {
    const colCount = this.isPortrait ? 2 : 3
    Sizes.tableFromList(this.buttons, colCount, this.current(this.biPositions.margin))
  }

  onInit(): void {
    this.onBiValueChange(biValues.scale, (c, v) => {
      c.symmetricScale = v
      this.createQuestion()
    })
  }

}



